  overlayHideShow() {
    this.overlayDisplay = !this.overlayDisplay;

  }
  filterTable() {
    $('#filter-btn').removeClass('btn-inactive').addClass('btn-active');
    var obj = this.filterForm.value;
    var count = 0;
    Object.keys(obj).forEach(function(k){
      if(obj[k] != null && obj[k] != ''){count++}
  });
    this.filter = "Cross Filter("+count+")";
    // this.studentList =[];
    this.studentList= this.studentListValue.filter(x => {
        return x.eng.toLowerCase().startsWith(this.filterForm.value.eng != null?this.filterForm.value.eng.toLowerCase():'') 
        && x.matricule.toLowerCase().startsWith(this.filterForm.value.matricule != null?this.filterForm.value.matricule.toLowerCase():"")
        && x.prec.toLowerCase().startsWith(this.filterForm.value.prec !=null?this.filterForm.value.prec.toLowerCase():'')
        && x.prev.toLowerCase().startsWith(this.filterForm.value.prev !=null?this.filterForm.value.prev.toLowerCase():'')
        && x.classe.toLowerCase().startsWith(this.filterForm.value.classe != null?this.filterForm.value.classe.toLowerCase():'');
      });
  }

  exitFilter(){
    this.filterForm.reset();
    $('#filter-btn').removeClass('btn-active').addClass('btn-inactive');
    this.filter = "Cross Filter";
    this.studentList = this.studentListValue;

  }

  selectedStudent: any;
  isEdit = false;

  pullEditSection() {
    if (this.isEdit) {
      this.isEdit = false;
      $('.student-list-table').removeClass('col-8').addClass('col-12 transition-05')
    } else {
      this.isEdit = true;
      $('.student-list-table').removeClass('col-12').addClass('col-8 transition-05')
    }
  }



